.container {
  font-family: Geometria, Roboto, Arial, sans-serif;
  min-height: 100vh;
  padding-top: 34px;
  color: #f2f2f2;
  font-style: normal;
  font-weight: 500;
  background: rgb(255 161 161);
  background: linear-gradient(180deg, rgb(255 161 161 / 100%) 0%, rgb(255 123 123 / 100%) 100%);

  @media screen and (max-width: 650px) {
    padding-top: 0;
  }
}
